.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 10px;
  padding-bottom: 100px; /* Увеличиваем отступ для BottomNav */
  background-color: #f4f6f8; /* Светлый фон для основной части */
  box-sizing: border-box;
}

.content {
  margin-top: 20px;
}

/* Стили для формы создания поста */
.createPostContainer {
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 15px;
  margin-bottom: 20px;
}

.postHeader {
  display: flex;
  gap: 12px;
}

.postAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.postForm {
  flex: 1;
}

.postInput {
  width: 100%;
  min-height: 60px;
  padding: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  resize: none;
  font-size: 15px;
  box-sizing: border-box;
  margin-bottom: 10px;
  font-family: inherit;
}

.postInput:focus {
  outline: none;
  border-color: #2a9d8f;
}

.postActions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.postAttachments {
  display: flex;
  gap: 10px;
}

.attachButton {
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  font-size: 18px;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.attachButton:hover {
  color: #2a9d8f;
}

.postSubmit {
  background-color: #2a9d8f;
  color: white;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s;
}

.postSubmit:hover {
  background-color: #238a7d;
}

.postSubmit:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

/* Стили для списка постов */
.postsContainer {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.post {
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 15px;
}

.postAuthorInfo {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.authorAvatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  overflow: hidden;
}

.authorAvatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.authorDetails {
  flex: 1;
}

.authorName {
  font-weight: 600;
  font-size: 15px;
  margin: 0;
  color: #333;
}

.postTime {
  font-size: 13px;
  color: #888;
  margin: 0;
}

.moreOptions {
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
  font-size: 16px;
  padding: 5px;
}

.postContent {
  margin-bottom: 15px;
}

.postText {
  font-size: 15px;
  line-height: 1.4;
  margin: 0;
  white-space: pre-wrap;
  word-break: break-word;
}

.postFooter {
  display: flex;
  gap: 15px;
  border-top: 1px solid #f1f1f1;
  padding-top: 10px;
}

.postAction {
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
  font-size: 14px;
  padding: 5px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.postAction:hover {
  color: #333;
}

/* Стили для пустого состояния */
.emptyState {
  background-color: white;
  padding: 30px 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-top: 20px;
}

.emptyState h2 {
  font-size: 20px;
  color: #333;
  margin: 0 0 10px 0;
}

.emptyState p {
  font-size: 16px;
  color: #888;
  margin: 0;
}

@media (min-width: 768px) {
  .container {
    max-width: 95%;
  }
  
  .postInput {
    min-height: 80px;
  }
} 